<template>
    <form>
        <input type="email" placeholder="login" v-model="login"/>
        <input type="password" placeholder="password" v-model="password"/>
        <input type="button" value="Sign in" @click="check">
    </form>
</template>

<script>
    import api from '../Api.js'

    export default {
        name: "Login",
        data () {
            return {
                login: '',
                password: ''
            }
        },
        methods: {
            check: function () {
                let user = {login: this.login, password: this.password};
                api.login(user);
                this.login = '';
                this.password='';
            }
        }
    }
</script>

